.footer {
  width: 100%;
  background-color: black;
}

.footer_p {
  font-family: "Montserrat", sans-serif;
  text-align: center;
  color: white;
  padding-top: 25px;
  padding-bottom: 25px;
}

@media screen and (min-width: 768px) {
  .footer_p {
    font-size: 18px;
  }
}

@media screen and (min-width: 1024px) {
  .footer{
    width: 100%;
  }
  
  .footer_p {
    padding-bottom: 30px;
    padding-top: 30px;
    font-size: 18px;
  }
}
