.container-projet {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  margin-bottom: 20px;
}

.container-projet-title {
  padding-bottom: 30px;
  font-family: "Montserrat", sans-serif;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 0;
}

@media (min-width: 1024px) {
  .container-projet {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  .container-projet-title {
    font-size: 35px;
    text-align: center;
    padding-top: 20px;
  }
}
