.modal {
  width: 80%;
  height: 20%;
  position: relative;
}

.modal_content {
  bottom: 110px;
  right: 8px;
  position: absolute;
  z-index: 1;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  background-color: white;
  border: 4px solid;
  border-image: linear-gradient(90deg, red, black) 1;
}
.modal_content_button {
  width: 20%;
  margin-bottom: 20px;
  border: 4px solid transparent;
  color: black;
  background-color: white;
  text-decoration: none;
  transition: all 0.3s;
  cursor: pointer;
  font-family: "Montserrat", sans-serif;
}
.modal_content_button {
  width: 26%;
  padding: 5px;
  border-bottom: 4px solid red;
  border-top: 4px solid red;
  font-weight: 800;
}

@media screen and (min-width: 768px) {
  .modal_content {
    right: 170px;
  }

  .modal_content_button {
    width: 30%;
  }
}
@media (min-width: 1024px) {
  .modal_content {
    right: 261px;
  }

  .modal_content_button {
    width: 15%;
  }
}
